*{
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
    font-family: 'Karla', sans-serif;
}
body{
    font-family: 'Karla';
    font-style: normal;
    font-weight: 700;
    font-size: 31.25px;
    line-height: 37px;
    text-align: center;

    color: #293264;

    height: 100vh;
    margin: 0;
    padding: 0;
    background-color: #F5F7FB;
    /* for bachground image to be displayed at bottom, height parameter must be declared */
    /* e.g. height:100vh; */
    background: url("../images/blob-bottom.png") bottom left no-repeat, 
        url("../images/blob-top.png") top right no-repeat;
    background-size: 10% , 15%;
}
.Intro-title{
    font-family: 'Karla';
    font-style: normal;
    font-weight: 700;
    font-size: 31.25px;
    line-height: 37px;
    text-align: center;
}
.Intro-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 85px;
}
.Intro-desc{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
}
.Intro-btn{
    width: 193px;
    height: 52px;
    background: #4D5B9E;
    border-radius: 15px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;

    color: #F5F7FB;
}
.Questions-group{
    /* height: 50px; */
}
@media (orientation: portrait ) {
    body{
        background-size: 20% ,25%;
    }
    .Intro-main{
        padding-top: 300px;
    }
  }